import { mapState, mapMutations } from 'vuex';
export default {
	data: function() {
		return {
			list: [],
			id: '',
			pageNo: 1,
			isReach: false,
			loadText: '加载中...'
		};
	},
	onload: function(option) {
		this.id = option.id;
	},
	onShow() {
		this.show();
	},
	computed: {
		...mapState(['app'])
	},
	onPullDownRefresh: function() {
		this.isReach = false;
		this.pageNo = 1;
		this.show();
	},
	onReachBottom: function() {
		console.log('加载');
		this.pageNo++;
		this.isReach = true;
		this.show();
	},
	methods: {
		...mapMutations(['urlRequest', 'TO', 'setStork']),
		show() {
			this.loadText = '加载中...';
			this.urlRequest({
				method: 'post',
				url: `/operation-agency-service/warehouseIn/getDetailList?access_token=${this.app.access_token}`,
				data: {
					isPage: true,
					pageNo: this.pageNo,
					pageSize: 10,
					params: { mid: this.id }
				},
				callBack: res => {
					let a = res.data.result;
					if (a === '0000' || !a.length) {
						this.loadText = '没有更多数据！';
						return;
					} else if (this.isReach) {
						for (let i of a) {
							this.list.push(i);
						}
						console.log(this.list);
						return;
					}
					this.list = a;
				}
			});
		}
	}
};
